import React, { useEffect, useRef, useState } from "react";
import {
  FaCaretDown,
  FaChevronCircleDown,
  FaChevronCircleUp,
  FaSearch,
} from "react-icons/fa";
import { CCollapse } from "@coreui/react";
import Loader from "../Loader/Loader";

import ProjectSearchTable from "./ProjectSearchTable";
import { MultiSelect } from "react-multi-select-component";
import axios from "axios";
import { environment } from "../../environments/environment";
import { ReactSearchAutocomplete } from "react-search-autocomplete";
import { SlExclamation } from "react-icons/sl";
import GlobalHelp from "../PrimeReactTableComponent/GlobalHelp";
import ScreenBreadcrumbs from "../Common/ScreenBreadcrumbs";

function ProjectSearch() {
  const loggedUserId = localStorage.getItem("resId");
  const [visible, setVisible] = useState(false);
  const [cheveronIcon, setCheveronIcon] = useState(FaChevronCircleUp);
  const [loader, setLoader] = useState(false);
  const [searching, setSearching] = useState(false);
  const [business, setBusiness] = useState([]);
  const [customer, setCustomer] = useState([]);
  const [manger, setManger] = useState([]);
  const [contract, setContract] = useState([]);
  const [SelectdContract, setSelectdContract] = useState([]);

  const [facilitator, setFacilitator] = useState([]);
  const [projectStage, setProjectStage] = useState([]);
  const [enguagementType, setEnguagementType] = useState([]);
  const [data, setData] = useState([{}]);
  const [selectedBusiness, setSelectedBusiness] = useState([]);
  const [linkColumns, setLinkColumns] = useState([]);
  const [flag, setFlag] = useState(false);
  const [linkColumnsRoutes, setLinkColumnsRoutes] = useState([]);
  const baseUrl = environment.baseUrl;
  const abortController = useRef(null);
  const HelpPDFName = "SearchProject.pdf";
  const Headername = "Project Search Help";

  const initialValue = {
    projectName: "",
    manager: -1,
    primaryBu: " 170,211,123,82,168,207,212,18,213,49,149,208",
    customer: -1,
    projectSource: null,
    projectStage: 1,
    po: null,
    teamLocation: null,
    engagementType: null,
    contractTeams: "28,27,752,606,26,25,750",
    FTE: 0,
    currentAllocation: 1,
    PCQAFacilitaor: "",
    teamsize: 1,
  };
  const [formData, setFormData] = useState(initialValue);
  const autoValues = {
    projectId: null,
    resId: "",
    isAll: 1,
  };
  const [value, setValue] = useState(autoValues);
  const handleClick = () => {
    const loaderTime = setTimeout(() => {
      setLoader(true);
    }, 2000);

    abortController.current = new AbortController();

    axios({
      method: "post",
      url: baseUrl + `/ProjectMS/project/getProjectSearch`,
      signal: abortController.current.signal,

      data: {
        projectName: formData.projectName,
        manager: formData.manager,
        primaryBu: formData.primaryBu,
        customer: formData.customer,
        projectSource: formData.projectSource,
        projectStage: formData.projectStage,
        po: formData.po,
        teamLocation: formData.teamLocation,
        engagementType: formData.engagementType,
        contractTeams: formData.contractTeams,
        FTE: formData.FTE,
        currentAllocation: formData.currentAllocation,
        PCQAFacilitaor: formData.PCQAFacilitaor,
      },
      headers: { "Content-Type": "application/json" },
    }).then((response) => {
      const data = response.data;

      data.sort((a, b) => {
        const nameA = a.project_code.toUpperCase();
        const nameB = b.project_code.toUpperCase();
        if (nameA < nameB) {
          return -1;
        }
        if (nameA > nameB) {
          return 1;
        }
        return 0;
      });

      if (formData.teamsize == 1 && formData.FTE == 0) {
        setFlag(false);

        let dataHeader = [
          {
            project_code: "Code",
            project_name: "Project Name",
            prj_manager: "Project Manager",
            sales_executive: "Sales Executive",
            prj_stage: "Project Stage",
            business_unit: "Primary_BU",
            po_number: "PO#",
            customer: "Customer",
            planned_start_dt: "Pln St Dt",
            planned_end_dt: "Pln End Dt",
            actual_start_dt: "Act St Dt",
            actual_end_dt: "Act End Dt",
            team_size: "Team Size",
          },
        ];
        setData(dataHeader.concat(data));
        clearTimeout(loaderTime);
        setVisible(!visible);
        visible
          ? setCheveronIcon(FaChevronCircleUp)
          : setCheveronIcon(FaChevronCircleDown);
        // }, 100);
      } else if (formData.teamsize == 1 && formData.FTE == 1) {
        setFlag(true);
        let dataHeader1 = [
          {
            contractor_onsite: "Contractor",
            employee_offshore: "Employee",
          },
        ];
        let dataHeader = [
          {
            project_code: "Code",
            project_name: "Project Name",
            prj_manager: "Project Manager",
            sales_executive: "Sales Executive",
            prj_stage: "Project Stage",
            business_unit: "Primary_BU",
            po_number: "PO#",
            customer: "Customer",
            planned_start_dt: "Pln St Dt",
            planned_end_dt: "Pln End Dt",
            actual_start_dt: "Act St Dt",
            actual_end_dt: "Act End Dt",
            contractor_onsite: "Onsite",
            contractor_offshore: "Offshore",
            employee_onsite: "Onsite",
            employee_offshore: "Offshore",
            team_size: "Team Size",
          },
        ];
        setData(dataHeader.concat(data));
        clearTimeout(loaderTime);
      } else if (formData.teamsize == 0 && formData.FTE == 1) {
        setFlag(true);

        let dataHeader = [
          {
            project_code: "Code",
            project_name: "Project Name",
            prj_manager: "Project Manager",
            sales_executive: "Sales Executive",
            prj_stage: "Project Stage",
            business_unit: "Primary_BU",
            po_number: "PO#",
            customer: "Customer",
            planned_start_dt: "Pln St Dt",
            planned_end_dt: "Pln End Dt",
            actual_start_dt: "Act St Dt",
            actual_end_dt: "Act End Dt",
            contractor_onsite: "Onsite",
            contractor_offshore: "Offshore",
            employee_onsite: "Onsite",
            employee_offshore: "Offshore",
          },
        ];
        setData(dataHeader.concat(data));
        clearTimeout(loaderTime);
      } else {
        let dataHeader = [
          {
            project_code: "Code",
            project_name: "Project Name",
            prj_manager: "Project Manager",
            sales_executive: "Sales Executive",
            prj_stage: "Project Stage",
            business_unit: "Primary_BU",
            po_number: "PO#",
            customer: "Customer",
            planned_start_dt: "Pln St Dt",
            planned_end_dt: "Pln End Dt",
            actual_start_dt: "Act St Dt",
            actual_end_dt: "Act End Dt",
          },
        ];
        setData(dataHeader.concat(data));
        clearTimeout(loaderTime);
      }
      let data1 = ["project_name"];
      let linkRoutes = ["/project/Overview/:project_id"];
      setLinkColumns(data1);
      setLinkColumnsRoutes(linkRoutes);
      setSearching(true);
    });
  };
  const handleAbort = () => {
    abortController.current && abortController.current.abort();
    setLoader(false);
  };
  const getBusinessUnit = () => {
    axios
      .get(baseUrl + `/CostMS/cost/getDepartments`)
      .then((Response) => {
        let countries = [];
        let data = Response.data;
        data.push({ value: 999, label: "Non-Revenue Units" });
        data.length > 0 &&
          data.forEach((e) => {
            let countryObj = {
              label: e.label,
              value: e.value,
            };
            countries.push(countryObj);
          });

        setBusiness(countries);
        let defaultSelected = JSON.parse(JSON.stringify(countries)).filter(
          (d) => d.label.includes("Non-Revenue Units") == false
        );

        setSelectedBusiness(defaultSelected);
      })
      .catch((error) => console.log(error));
  };
  const getcontractTerms = () => {
    axios
      .get(baseUrl + `/ProjectMS/ProjectScopeChange/getContractTerms`)
      .then((Response) => {
        let countries = [];
        let data = Response.data;
        data.length > 0 &&
          data.forEach((e) => {
            let countryObj = {
              label: e.lkup_name,
              value: e.id,
            };
            countries.push(countryObj);
          });
        setContract(countries);

        let defaultSelected = JSON.parse(JSON.stringify(countries)).filter(
          (d) => d.label.includes("Non-billable") == false
        );

        setSelectdContract(defaultSelected);
      })
      .catch((error) => console.log(error));
  };
  const customerFnc = (ids) => {
    // const string = ids.join(", ");
    let string = "";

    if (Array.isArray(ids)) {
      string = ids.join(", ");
    }

    axios({
      method: "get",
      url:
        baseUrl +
        `/ProjectMS/ProjectScopeChange/getAllCustomers?buid=${formData.primaryBu}`,
    }).then((res) => {
      let custom = res.data;
      setCustomer(custom);
    });
  };

  const fcqaFacilitatorFnc = () => {
    axios({
      method: "get",
      url: baseUrl + `/ProjectMS/risks/getAssignedData`,
    })
      .then((res) => {
        let manger = res.data;
        setFacilitator(manger);
      })
      .catch((error) => {});
  };
  const projectStageFnc = () => {
    axios({
      method: "get",
      url: baseUrl + `/ProjectMS/ProjectScopeChange/getProjectStage`,
    })
      .then((res) => {
        let data = res.data;
        setProjectStage(data);
      })
      .catch((error) => {});
  };
  const enguagementTypeFnc = () => {
    axios({
      method: "get",
      url: baseUrl + `/ProjectMS/ProjectScopeChange/getEnguagementType`,
    })
      .then((res) => {
        let data = res.data;
        setEnguagementType(data);
      })
      .catch((error) => {});
  };
  const managerFnc = () => {
    axios({
      method: "post",
      url: baseUrl + `/ProjectMS/project/getResListForAutoCmplt`,
      data: {
        projectId: null,
        resId: "",
        isAll: 1,
      },
      headers: { "Content-Type": "application/json" },
    }).then((response) => {
      const data = response.data;

      setManger(data);
    });
  };
  useEffect(() => {
    getBusinessUnit();
    customerFnc();
    fcqaFacilitatorFnc();
    projectStageFnc();
    enguagementTypeFnc();
    managerFnc();
    getcontractTerms();
  }, []);
  useEffect(() => {
    customerFnc();
  }, [formData.primaryBu]);

  const getName = (e) => {
    const { value, id } = e.target;
    setFormData({ ...formData, projectName: value });
  };
  const ArrowRenderer = ({ expanded }) => (
    <>
      {expanded ? (
        <FaCaretDown className="chevronIcon" />
      ) : (
        <FaCaretDown className="chevronIcon" />
      )}
    </>
  );

  const handleClear = () => {
    setFormData((prev) => ({ ...prev, manager: null }));
  };
  const handleClear2 = () => {
    setFormData((prev) => ({ ...prev, PCQAFacilitaor: null }));
  };
  const [routes, setRoutes] = useState([]);
  let textContent = "Delivery";
  let currentScreenName = ["Project Search"];

  useEffect(() => {
    getMenus();
  }, []);

  const getMenus = () => {
    axios({
      method: "GET",
      url: baseUrl + `/CommonMS/master/getMenus?loggedUserId=${loggedUserId}`,
    }).then((resp) => {
      let data = resp.data;

      data.forEach((item) => {
        if (item.display_name === textContent) {
          setRoutes([item]);
        }
      });
    });
  };

  const generateDropdownLabel = (selectedOptions, allOptions) => {
    const selectedValues = selectedOptions.map((option) => option.value);

    const allValues = allOptions.map((item) => item.value);

    if (selectedValues.length === allValues.length) {
      return "<< ALL >>";
    } else {
      return selectedOptions.map((option) => option.label).join(", ");
    }
  };
  return (
    <div>
      <div className="col-md-12">
        <div className="pageTitle">
          <div className="childOne"></div>
          <div className="childTwo">
            <h2>Project Search</h2>
          </div>
          <div className="childThree"></div>
        </div>
        <ScreenBreadcrumbs
          routes={routes}
          currentScreenName={currentScreenName}
        />
      </div>
      <div className="group mb-3 customCard">
        <div className="col-md-12 collapseHeader">
          <h2>Search Filters</h2>
          <div className="helpBtn">
            <GlobalHelp pdfname={HelpPDFName} name={Headername} />
          </div>
          <div
            onClick={() => {
              setVisible(!visible);
              visible
                ? setCheveronIcon(FaChevronCircleUp)
                : setCheveronIcon(FaChevronCircleDown);
            }}
          >
            <span>{cheveronIcon}</span>
          </div>
        </div>
        <CCollapse visible={!visible}>
          <div className="group-content row">
            <div className=" col-md-3 mb-2">
              <div className="form-group row">
                <label className="col-5" htmlFor="projectname">
                  Project Name
                </label>
                <span className="col-1 p-0">:</span>
                <div className="col-6">
                  <input
                    type="text"
                    className="form-control"
                    id="projectName"
                    placeholder
                    onChange={(e) => {
                      getName(e);
                    }}
                  />
                </div>
              </div>
            </div>
            <div className=" col-md-3 mb-2">
              <div className="form-group row">
                <label className="col-5" htmlFor="manager">
                  Manager
                </label>
                <span className="col-1 p-0">:</span>
                <div className="col-6">
                  <div className="autoComplete-container">
                    <ReactSearchAutocomplete
                      items={manger}
                      type="Text"
                      name="manager"
                      id="manager"
                      className="err"
                      fuseOptions={{ keys: ["id", "ResName"] }}
                      resultStringKeyName="ResName"
                      // placeholder="Type minimum 3 characters to get the list"
                      resource={manger}
                      managerFnc={managerFnc}
                      onSelect={(e) => {
                        setFormData((prevProps) => ({
                          ...prevProps,
                          manager: e.id,
                        }));
                      }}
                      showIcon={false}
                      onClear={handleClear}
                    />
                  </div>
                </div>
              </div>
            </div>
            <div className=" col-md-3 mb-2">
              <div className="form-group row">
                <label className="col-5" htmlFor="primaryBu">
                  Business Unit
                </label>
                <span className="col-1 p-0">:</span>
                <div className="col-6">
                  <MultiSelect
                    id="primaryBu"
                    options={business}
                    hasSelectAll={true}
                    value={selectedBusiness}
                    ArrowRenderer={ArrowRenderer}
                    disabled={false}
                    overrideStrings={{
                      selectAllFiltered: "Select All",
                      selectSomeItems: "<< All>>",
                    }}
                    onChange={(e) => {
                      setSelectedBusiness(e);
                      let filteredCountry = [];
                      e.forEach((d) => {
                        filteredCountry.push(d.value);
                      });
                      setFormData((prevVal) => ({
                        ...prevVal,
                        ["primaryBu"]: filteredCountry.toString(),
                      }));
                      customerFnc(filteredCountry);
                    }}
                    valueRenderer={generateDropdownLabel}
                  />
                </div>
              </div>
            </div>
            <div className=" col-md-3 mb-2">
              <div className="form-group row">
                <label className="col-5" htmlFor="customer">
                  Customer
                </label>
                <span className="col-1 p-0">:</span>
                <div className="col-6">
                  <select
                    className=""
                    id="customer"
                    onChange={(e) => {
                      const { value, id } = e.target;
                      setFormData({ ...formData, [id]: value });
                    }}
                  >
                    <option value="-1"> &lt;&lt;ALL&gt;&gt;</option>
                    {customer.map((Item) => (
                      <option value={Item.customerId}> {Item.customers}</option>
                    ))}
                  </select>
                </div>
              </div>
            </div>
            <div className=" col-md-3 mb-2">
              <div className="form-group row">
                <label className="col-5" htmlFor="projectsource">
                  Project Source
                </label>
                <span className="col-1 p-0">:</span>
                <div className="col-6">
                  <select
                    id="projectSource"
                    onChange={(e) => {
                      const { value, id } = e.target;
                      setFormData({ ...formData, [id]: value });
                    }}
                  >
                    <option value="null">&lt;&lt;ALL&gt;&gt;</option>
                    <option value="PPM">PPM</option>
                    <option value="Projector">Projector</option>
                  </select>
                </div>
              </div>
            </div>
            <div className=" col-md-3 mb-2">
              <div className="form-group row">
                <label className="col-5" htmlFor="projectstage">
                  Project Stage
                </label>
                <span className="col-1 p-0">:</span>
                <div className="col-6">
                  <select
                    id="projectStage"
                    onChange={(e) => {
                      const { value, id } = e.target;
                      const selectedValue = value === "1" ? null : value; // Check if "-1" is selected and set selectedValue accordingly
                      setFormData({ ...formData, [id]: selectedValue });
                    }}
                  >
                    <option value="1"> &lt;&lt;ALL&gt;&gt;</option>
                    {projectStage.map((Item) => (
                      <option value={Item.id}> {Item.lkup_name}</option>
                    ))}
                  </select>
                </div>
              </div>
            </div>
            <div className=" col-md-3 mb-2">
              <div className="form-group row">
                <label className="col-5" htmlFor="po">
                  PO#
                </label>
                <span className="col-1 p-0">:</span>
                <div className="col-6">
                  <select
                    id="po"
                    onChange={(e) => {
                      const { value, id } = e.target;
                      setFormData({ ...formData, [id]: value });
                    }}
                  >
                    With PO Number
                    <option value="null">&lt;&lt;ALL&gt;&gt;</option>
                    <option value="1">With PO Number</option>
                    <option value="0">Without PO Number</option>
                  </select>
                </div>
              </div>
            </div>
            <div className=" col-md-3 mb-2">
              <div className="form-group row">
                <label className="col-5" htmlFor="teamlocation">
                  Team Location
                </label>
                <span className="col-1 p-0">:</span>
                <div className="col-6">
                  <select
                    id="teamLocation"
                    // onChange={(e) => {
                    //   const { value, id } = e.target;
                    //   setFormData({ ...formData, [id]: value });
                    // }}
                    onChange={(e) => {
                      const { value, id } = e.target;
                      const selectedValue = value === "null" ? null : value;
                      setFormData({ ...formData, [id]: selectedValue });
                    }}
                  >
                    <option value="null">&lt;&lt;ALL&gt;&gt;</option>
                    <option value="361">Client Site</option>
                    <option value="362">Onsite</option>
                    <option value="363">Off Shore</option>
                    <option value="364">Blended Shore</option>
                  </select>
                </div>
              </div>
            </div>
            <div className=" col-md-3 mb-2">
              <div className="form-group row">
                <label className="col-5 " htmlFor="engagementtype">
                  Engagement Type
                </label>
                <span className="col-1 p-0">:</span>
                <div className="col-6">
                  <select
                    id="engagementType"
                    // onChange={(e) => {
                    //   const { value, id } = e.target;
                    //   setFormData({ ...formData, [id]: value });
                    // }}
                    onChange={(e) => {
                      const { value, id } = e.target;
                      const selectedValue = value === "All" ? null : value;
                      setFormData({ ...formData, [id]: selectedValue });
                    }}
                  >
                    <option value="All"> &lt;&lt;ALL&gt;&gt;</option>
                    {enguagementType.map((Item) => (
                      <option value={Item.id}> {Item.lkup_name}</option>
                    ))}
                  </select>
                </div>
              </div>
            </div>
            <div className=" col-md-3 mb-2">
              <div className="form-group row">
                <label className="col-5" htmlFor="contractterms">
                  Contract Terms
                </label>
                <span className="col-1 p-0">:</span>
                <div className="col-6">
                  <MultiSelect
                    id="contractTeams"
                    options={contract}
                    hasSelectAll={true}
                    ArrowRenderer={ArrowRenderer}
                    value={SelectdContract}
                    disabled={false}
                    overrideStrings={{
                      selectAllFiltered: "Select All",
                      selectSomeItems: "<< All>>",
                    }}
                    onChange={(e) => {
                      setSelectdContract(e);
                      let filteredCountry = [];
                      e.forEach((d) => {
                        filteredCountry.push(d.value);
                      });
                      setFormData((prevVal) => ({
                        ...prevVal,
                        ["contractTeams"]: filteredCountry.toString(),
                      }));
                    }}
                    valueRenderer={generateDropdownLabel}
                  />
                </div>
              </div>
            </div>
            <div className=" col-md-3 mb-2">
              <div className="form-group row">
                <label className="col-5" htmlFor="fte">
                  FTE
                </label>
                <span className="col-1 p-0">:</span>
                <div className="col-6">
                  <select
                    id="FTE"
                    onChange={(e) => {
                      const { value, id } = e.target;
                      setFormData({ ...formData, [id]: value });
                    }}
                  >
                    <option value="0" selected="selected">
                      No
                    </option>
                    <option value="1">Yes</option>
                  </select>
                </div>
              </div>
            </div>
            <div className=" col-md-3 mb-2">
              <div className="form-group row">
                <label className="col-5" htmlFor="teamsize">
                  Team Size
                </label>
                <span className="col-1 p-0">:</span>
                <div className="col-6">
                  <select
                    id="teamsize"
                    onChange={(e) => {
                      const { value, id } = e.target;
                      setFormData({ ...formData, [id]: value });
                    }}
                  >
                    <option value="0">No</option>
                    <option value="1" selected="selected">
                      Yes
                    </option>
                  </select>
                </div>
              </div>
            </div>
            <div className=" col-md-3 mb-2">
              <div className="form-group row">
                <label className="col-5 " htmlFor="currentallocation">
                  Current Allocation
                </label>
                <span className="col-1 p-0">:</span>
                <div className="col-6">
                  <select
                    id="currentAllocation"
                    onChange={(e) => {
                      const { value, id } = e.target;
                      const selectedValue = value === "null" ? -1 : value;
                      setFormData({ ...formData, [id]: selectedValue });
                    }}
                  >
                    <option value="null">&lt;&lt;ALL&gt;&gt;</option>
                    <option value="1" selected="selected">
                      Active
                    </option>
                    <option value="2">InActive</option>
                    <option value="3">No Allocation</option>
                  </select>
                </div>
              </div>
            </div>
            <div className=" col-md-3 mb-2">
              <div className="form-group row">
                <label className="col-5" htmlFor="facilitator">
                  PCQA Facilitator
                </label>
                <span className="col-1 p-0">:</span>
                <div className="col-6">
                  <div className="autoComplete-container">
                    <ReactSearchAutocomplete
                      items={facilitator}
                      type="Text"
                      name="PCQAFacilitaor"
                      id="PCQAFacilitaor"
                      className="err"
                      fuseOptions={{ keys: ["id", "name"] }}
                      resultStringKeyName="name"
                      // placeholder="Type minimum 3 characters to get the list"
                      resource={facilitator}
                      fcqaFacilitatorFnc={fcqaFacilitatorFnc}
                      onSelect={(e) => {
                        setFormData((prevProps) => ({
                          ...prevProps,
                          PCQAFacilitaor: e.id,
                        }));
                      }}
                      showIcon={false}
                      onClear={handleClear2}
                    />
                  </div>
                </div>
              </div>
            </div>

            <div className="col-md-12 col-sm-12 col-xs-12 my-2 btn-container center">
              <button
                type="button"
                className="btn btn-primary"
                onClick={handleClick}
              >
                <FaSearch /> Search{" "}
              </button>
            </div>
          </div>
        </CCollapse>
        {loader ? <Loader handleAbort={handleAbort} /> : ""}
      </div>
      <div className="col-md-12 my-1">
        <div
          className="statusMsg
                    warning"
        >
          <SlExclamation />
          Note: Values for projector projects are for Jan-17 to +6 months from
          current month.
        </div>
      </div>
      {searching ? (
        <ProjectSearchTable
          formData={formData}
          data={data}
          linkColumns={linkColumns}
          linkColumnsRoutes={linkColumnsRoutes}
          flag={flag}
        />
      ) : (
        " "
      )}
    </div>
  );
}

export default ProjectSearch;
